*, :before, :after {
  box-sizing: border-box;
}

* {
  font: inherit;
  margin: 0;
  padding: 0;
}

html {
  color-scheme: dark light;
}

body {
  min-height: 100vh;
}

img, picture, svg, video {
  max-width: 100%;
  display: block;
}

:root {
  --color-white: #fff;
  --color-black: #333;
  --color-blue: #478cf3;
  --color-blue_hover: #3c84f1;
  --color-blue-light: #e4edfd;
  --color-purple: #a200ff;
  --color-red: red;
  --color-gray: #eaeaea;
  --color-gray-light: #f8f8f8;
  --color-gray-dark: #999;
}

:root {
  font-family: Roboto, sans-serif;
  font-weight: normal;
}

* {
  font-size: 12px;
}

body {
  gap: 0 20px;
  display: grid;
  position: relative;
}

#header, #main, #footer {
  flex-direction: column;
  display: flex;
}

#main {
  grid-area: main;
}

p, span, h1, h3, h3, label {
  color: var(--color-black);
}

h2 {
  margin: 1.5rem 0 0;
  font-size: 1.33rem;
  font-weight: 600;
}

a {
  color: var(--color-blue);
  cursor: pointer;
  font-size: .9rem;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

a.extra {
  color: var(--color-gray-dark);
  padding-right: 15px;
  font-size: 1rem;
  position: relative;
}

a.extra:after {
  content: "❯";
  position: absolute;
  -webkit-transform: translate(3px, -1px);
  transform: translate(3px, -1px);
}

.warning {
  color: var(--color-red);
}

#footer {
  color: rgba(0, 0, 0, 0);
  background: linear-gradient(90deg, var(--color-blue), var(--color-purple), var(--color-blue), var(--color-purple));
  background-size: 300% 100%;
  -webkit-background-clip: text;
  background-clip: text;
  grid-area: footer;
  align-content: center;
  justify-content: center;
  padding: 0 10px;
  font-size: smaller;
  font-weight: lighter;
  -webkit-animation: 10s infinite gradient;
  animation: 10s infinite gradient;
}

@-webkit-keyframes gradient {
  100% {
    background-position: 100% 0;
  }
}

@keyframes gradient {
  100% {
    background-position: 100% 0;
  }
}

.input-field {
  width: 100%;
  flex-direction: column;
  display: flex;
}

.input-field input:disabled {
  background-color: inherit;
}

.input-field input:hover, .input-field input:focus {
  outline: none;
}

.input-field_theme-standard label {
  color: var(--color-gray-dark);
  font-size: .75rem;
}

.input-field_theme-standard label + input {
  margin-bottom: 14px;
}

.input-field_theme-standard input {
  border: none;
  border-bottom: 1px solid var(--color-blue);
  outline: 0;
  padding: 7px 0;
}

.input-field_theme-inline {
  flex-direction: row;
  position: relative;
  overflow: hidden;
}

.input-field_theme-inline input:focus {
  color: var(--color-black);
}

.input-field_theme-inline:not(:last-child):before, .input-field_theme-inline:not(:last-child):after {
  content: "";
  height: 1px;
  width: 100%;
  transition: -webkit-transform .3s, transform .3s;
  position: absolute;
  bottom: 0;
}

.input-field_theme-inline:before {
  background-color: var(--color-gray);
  -webkit-transform: translate(0);
  transform: translate(0);
}

.input-field_theme-inline:not(:last-child):focus-within:before {
  -webkit-transform: translate(-100%);
  transform: translate(-100%);
}

.input-field_theme-inline:after {
  background-color: var(--color-blue);
  -webkit-transform: translate(100%);
  transform: translate(100%);
}

.input-field_theme-inline:not(:last-child):focus-within:after {
  -webkit-transform: translate(0);
  transform: translate(0);
}

.input-field_theme-inline label, .input-field_theme-inline input {
  padding: 10px 0;
}

.input-field_theme-inline label {
  width: 100%;
}

.input-field_theme-inline input {
  text-align: right;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  color: var(--color-gray-dark);
  border: none;
  margin-left: auto;
}

.input-field_theme-primary input {
  color: var(--color-black);
  background-color: #efefef;
  border: none;
  border-radius: 5px;
  outline: none;
  padding: 10px 10px 10px 20px;
}

.input-field_theme-primary input:focus::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0);
}

.input-field_theme-primary input:focus::placeholder {
  color: rgba(0, 0, 0, 0);
}

.input-field_theme-primary input[type="search"] {
