}

.input-field_theme-primary input[type="search"], .input-field_theme-primary input[type="search"]::-webkit-input-placeholder {
  background-position: 10px;
  background-repeat: no-repeat;
  background-size: 1em;
  transition: background-image .1s, color .1s;
}

.input-field_theme-primary input[type="search"], .input-field_theme-primary input[type="search"]::placeholder {
  background-position: 10px;
  background-repeat: no-repeat;
  background-size: 1em;
  transition: background-image .1s, color .1s;
}

.input-field_theme-primary input[type="search"] {
  background-image: var(--bg-transparent);
  padding-left: 30px;
}

.input-field_theme-primary input[type="search"]:focus, .input-field_theme-primary input[type="search"]:active, .input-field_theme-primary input[type="search"]:not(:placeholder-shown) {
  background-image: var(--bg);
  transition: all .2s;
}

.input-field_theme-primary input[type="search"]::-webkit-input-placeholder {
  color: var(--color-gray-dark);
  background-image: var(--bg);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 1em;
  margin: auto;
  padding-left: 30px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -30px;
  right: 0;
}

.input-field_theme-primary input[type="search"]::placeholder {
  color: var(--color-gray-dark);
  background-image: var(--bg);
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: 1em;
  margin: auto;
  padding-left: 30px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: -30px;
  right: 0;
}

.input-field_theme-primary input[type="search"]:focus::-webkit-input-placeholder {
  color: rgba(0, 0, 0, 0);
  background-image: var(--bg-transparent);
}

.input-field_theme-primary input[type="search"]:focus::placeholder {
  color: rgba(0, 0, 0, 0);
  background-image: var(--bg-transparent);
}

.input-field_theme-primary input[type="search"]::-webkit-search-cancel-button {
  display: none;
}

a .mb-icon, button .mb-icon {
  margin: 0 auto;
  display: flex;
}

.mb-icon {
  --size: 1em;
  width: var(--size);
  height: var(--size);
  background-size: var(--size);
  background-position: 50%;
  background-repeat: no-repeat;
  display: inline-block;
}

.mb-pointer {
