.avatar > img {
  background: linear-gradient(90deg, var(--color-blue), var(--color-purple), var(--color-blue), var(--color-purple));
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
  background-size: 300% 100%;
  border-radius: 50%;
  -webkit-animation: 5s infinite gradient;
  animation: 5s infinite gradient;
}

.avatar_editable {
  cursor: pointer;
  --delay: .5s;
  box-shadow: 0 0 0 2px var(--color-gray);
  transition: box-shadow var(--delay);
  border-radius: 50%;
  position: relative;
}

.avatar_editable:hover {
  box-shadow: 0 0 0 2px var(--color-blue);
}

.avatar_editable:after {
  content: "Изменить аватар";
  height: 100%;
  width: 100%;
  border-radius: inherit;
  color: var(--color-white);
  background-color: var(--color-black);
  opacity: .5;
  transition: opacity var(--delay);
  flex-wrap: wrap;
  align-content: center;
  font-size: 1.3rem;
  line-height: 1.3rem;
  display: flex;
  position: absolute;
  top: 0;
}

.avatar_editable:hover:after {
  background-color: var(--color-black);
  opacity: .8;
}

.button {
  cursor: pointer;
  color: var(--color-white);
  background-color: var(--color-blue);
  border: none;
  padding: 1em;
  text-decoration: none;
}

.button:hover {
  background-color: var(--color-blue_hover);
}

.button_theme-standard {
  min-width: 280px;
  border-radius: 8px;
  font-size: 1.1rem;
}

.button_theme-icon {
  border-radius: 50%;
}

* {
  font-size: 13px;
}

.profile {
  display: inherit;
  flex-direction: inherit;
  gap: 60px;
  margin: 60px 0;
}

.profile__summary, .profile__full, .profile__settings, .profile__submit {
  margin: 0 auto;
}

.profile__summary {
  text-align: center;
}

.profile__full, .profile__settings {
  width: 100%;
}

.profile__settings > * {
  flex-direction: row;
  margin-bottom: 10px;
  padding-bottom: 10px;
  display: block;
}

.profile__settings > :not(:last-child) {
  border-bottom: 1px solid var(--color-gray);
}

nav.backlink-wrapper {
  width: 65px;
  height: 100%;
  background-color: var(--color-gray-light);
  border-right: 1px solid var(--color-gray);
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

nav.backlink-wrapper .backlink {
  height: inherit;
  width: inherit;
  display: inherit;
  justify-content: inherit;
  align-items: inherit;
}

nav.backlink-wrapper .mb-icon {
  background-size: 1em;
  background-color: var(--color-blue);
  width: 3rem;
  height: 3rem;
  border-radius: 100%;
  padding: 1em;
}

nav.backlink-wrapper:hover .mb-icon {
  background-color: var(--color-blue_hover);
}

